
* {
    box-sizing: border-box;
    text-decoration: none;

}

a {
    outline: none;
}

a:focus {
    outline: none;
    /* text-decoration: none;
    border: none; */
}


button {
    cursor: pointer;
}

.splash-back {
    
    width: 100%;
    height: 100vh;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    background-color: #1d3557;
    /* position: relative; */
}

.splash-title{
    width: 100%;
    height: 10%;
    /* height: 5em; */
    background-color: #023e8a;
    /* position: absolute; */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    font-size: 1.75em;
    padding: .5em;

}

.splash-title > h1 {
    color: white;
}

.splash-body {
    width: 60%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: skyblue; */
}
.splash-left {
    width: 60%;
    height: 80%;
    background-color: #457b9d;
    background-image: url('https://i.imgur.com/LEdYIrm.png?1');
    /* background-color: aqua; */
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.splash-right{
    background-color: #caf0f8;
    width: 40%;
    height: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.splash-headline{
    width: 100%;
    height: 10%;
    /* border: 4px solid black; */
    display: flex;
    /* align-items: center; */
    justify-content: center;
    font-size: 1.5em;
    border-bottom: 1px rgba(0, 0, 0, 0.089) solid;

}
    .splash-headline>h1{
        font-size: 30px;
        /* text-decoration: underline; */

    }

.splash-subtext{
    width: 90%;
    font-size: 20px;
    height: 50%;
    /* border: 4px solid black; */
    /* display: flex; */
    /* align-items: center; */
    /* justify-content: center; */
    flex-direction: column;

    
}

.splash-button{
    width: 100%;
    height: 20%;
    /* border: 4px solid black; */
    display: flex;
    align-items: center;
    justify-content: center;

}
    .splash-button> button{
        /* border: none; */
        background-color: #00b4d800;
        outline: none;
        /* color: white; */
        /* width: 12em;
        height: 4em; */
        font-size: 16px;
        padding: .25em 1em .25em 1em;
        border: #1d3557 .1em solid;
        transition-duration: 1s;

    }
    .splash-button> button:hover{
        background-color: #1d3557;
        color: white;
        transition-duration: 1s;

    }

    .tasklist::-webkit-scrollbar {
        display: none;
    }





/* ///////////    Sidebar                  /////////////////// */

.navbar {
    /* margin-top: 5em; */
    width: 5em;
    height: 100vh;
    position: fixed;
    background-color: #2396dd;
    transition: width ease 200ms;
    display: flex;
    flex-direction: column;
    top:0
}

.navbar-nav {
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-item {
    width: 100%;
    /* background-color: whitesmoke ; */
    transition: ease 0.5s;
    outline: none;
    border: none;


}
.nav-item:hover {
    width: 100%;
    background-color: #03055e8a;
    transition: ease 0.5s;
    fill: white;
    text-decoration: none;
        outline: none;




}

.nav-link {
    border-style: none;
        outline: none;
    border: none;
    display: flex;
    align-items: center;
    height: 5em;
    color: white;
    text-decoration: none;
    /* justify-content: center; */
}


.link-text {
    display: none;
    margin-left: 1em;
    text-decoration: none;
    color: white;
        outline: none;


}

.nav-link > svg {
    min-width: 2.5em;
    margin: 0 1em;
    outline: none;
    border: none;
}

svg:hover {
   fill: #e63946;
}

.navbar:hover {
    width: 15em;
    transition: width ease 200ms;
}

.navbar:hover .link-text {
    display: inline-flex ;
    text-decoration: none;

    
}



#toMany {
    background-color: #e63946;
    color: white;

}


/* RESOURCE */

.resource-all {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    /* background-color: wheat; */
    /* justify-content: center; */
    align-items: center;
}

.resource-title{
    
    /* width: 100%; */
    /* background-color: teal; */
    display: flex;
    align-items: center;
    /* justify-content: center; */
    font-size: 3em;
    margin: 1em;

}

.resource-body{
    /* height: 65%; */
    /* width: 100%; */
    /* background-color: whitesmoke; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5em;
}

.resource-item {
    margin: 1em;
    border: solid 2px;
    height: 20em;
    width: 20em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
    transition: ease 1s;

}


.resource-item:hover {
    background-color: #2396dd71;
    transition: ease 1s;
    
}

.resource-item > svg {
    margin: 0;
    height: 50%;
}


.resource-link {
    text-decoration: none;
    border: none;
    outline: none;
    margin: 0;
}

.item-title{
    margin: 0;
}


.index-all{
    height: 100vh;
    position: relative;
}